html {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
}

.people {
  grid-template-columns: repeat(2, 200px);
  place-content: center;
  gap: 40px 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.people__person {
  width: 100%;
  transition-property: background-color border-radius width;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition-duration: 1s;
  display: flex;
  position: relative;
}

.people__person:hover {
  transition-property: background-color border-radius width;
  background-color: #eee;
  border-radius: 12px;
  width: 220px;
  transition-duration: 1s;
}

.people__person:hover .people__person-extra {
  justify-content: center;
  align-self: center;
  align-items: center;
  display: flex;
}

.people__person:hover .people__person-photo {
  border-radius: 60px;
}

.people__person-photo {
  background-position: center;
  border-radius: 50%;
  width: 100%;
  transition-duration: 1s;
}

.people__person-name {
  color: #2f4f4f;
  font-weight: 700;
}

.people__person-rank {
  color: #8b0000;
  background: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.people__person-extra {
  display: none;
}
